pt type=\"text/ecmascript-6\">\nimport page from '../../common.js';\nexport default {\n  data () {\n    return {\n      money: '',\n      m: '',\n      token: window.sessionStorage.getItem('bbytoken'),\n      authList: [],\n      curId: 0,\n      seCurId: 0,\n      threeAuthList: \"\"\n    }\n  },\n  created () {\n    this.$axios.post('admin/SmallSystemSet/selectMoney', {\n      token: this.token\n    })\n      .then(res => {\n\n        if (res.data.code == 0) //没有值 需要设置\n          layer.alert(res.data.msg);\n        else {\n          this.money = res.data.data.am.money;\n          this.m = 1; //有值 显示修改\n        }\n      }).catch(err => console.log(err))\n  },\n  methods: {\n    pass () { //设置\n      this.$axios.post('admin/SmallSystemSet/setMoney', {\n        token: this.token,\n        money: this.money\n      })\n        .then(res => {\n\n          layer.msg(res.data.msg);\n        }).catch(err => { })\n    },\n    edit () { //修改\n      this.$axios.post('admin/SmallSystemSet/alterMoney', {\n        token: this.token,\n        money: this.money\n      })\n        .then(res => {\n\n          layer.msg(res.data.msg);\n        }).catch(err => { })\n    }\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n   \n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == '邦保养') {\n              this.seCurId = arr[i].id;\n            }\n\n            if (arr[i].son) {\n              this.threeAuthList = arr[i].son\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n\n                if (arr[i].son[j].name == '车主分享奖励' && arr[i].name == '邦保养') {\n                  this.thCurId = arr[i].son[j].id;\n\n                }\n\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  }\n}\n</script>\n<style scoped>\n.content {\n  overflow: hidden;\n  width: 330px;\n  margin: 200px auto;\n  font-size: 20px;\n}\n\n.content input {\n  display: inline-block;\n  width: 100px;\n}\n\n.content button {\n  margin-top: 50px;\n  float: right;\n  width: 100px;\n  height: 36px;\n  border-radius: 18px;\n  color: white;\n  background-color: #00b4ff;\n  display: block;\n}\n</style>"],"sourceRoot":""}]);

